.gptxxx_blog {
    display: flex;
    flex-direction: column;
}
.gptxxx_blog-heading {
    width: 100%;
    text-align: left;
    margin-bottom: 5rem;
}
.gptxxx_blog-heading h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;
}

.gptxxx_blog-container {
    display: flex;
    flex-direction: row;
}
.gptxxx_blog-container_gpA {
    flex: 0.75;
    margin-right: 2rem;
}
.gptxxx_blog-container_gpB {
    flex: 1;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
}

@media screen and (max-width: 990px) {
    .gptxxx_blog-container {
        /* flex-direction: column; */
        flex-direction: column-reverse;
    }

    .gptxxx_blog-container_gpA {
        margin: 2rem 0;
    }

    .gptxxx_blog-container_gpA .gptxxx_blog-container_article {
        width: 48%;
    }

     .gptxxx_blog-container_gpA .gptxxx_blog-container_article-image {
        height: 250px;
    }
}

@media screen and (max-width: 700px) {
    .gptxxx_blog-container_gpB {
        grid-template-columns: repeat(1, 1fr);
    }

    .gptxxx_blog-container_gpA .gptxxx_blog-container_article {
        width: 100%;
    }

    .gptxxx_blog-heading h1 {
        font-size: 46px;
        line-height: 50px;
    }
}

@media screen and (max-width: 550px) {
    .gptxxx_blog-heading h1 {
        font-size: 34px;
        line-height: 40px;
    }
}